.packages-container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 10px;
    margin-left: 10px;
    margin-top: 100px;
    
}
@media only screen and (max-width: 688px) {
    .packages-container{
        grid-template-columns: repeat(1,1fr);
    }
}

@media only screen and (min-width: 689px) and (max-width: 991px) {
    .packages-container{
        grid-template-columns: repeat(2,1fr);
    }

}